import React, { useEffect } from "react";
import { socket } from "./SockectConnect";
import { useSelector, useDispatch } from "react-redux";
import { FetchClients, FetchStaffs } from "../Network/CredApi";
import { FetchModes } from "../Network/ModeApi";
import { FetchExpenseCats } from "../Network/ExpenseCatApi";
import { FetchExpenses } from "../Network/ExpenseApi";
import { FetchAccounts } from "../Network/AccountApi";
import { FetchApplications } from "../Network/ApplicationApi";
import { FetchBranchs } from "../Network/BranchApi";
import { FetchEntrys } from "../Network/EntryApi";
import { FetchGroups } from "../Network/GroupApi";
import { FetchSubs } from "../Network/SubApi";
import { FetchMpesaPayments } from "../Network/MpesaApi";
import { FetchMessages } from "../Network/MessageApi";
import { FetchNotifications } from "../Network/NotificationApi";
import { FetchPayrollCats } from "../Network/PayrollCatApi";
import { FetchPayrollEntries } from "../Network/PayrollEntryApi";
import { FetchPayrollItems } from "../Network/PayrollItemApi";

const SocketMessages = () => {
	//redux dispatch
	const dispatch = useDispatch();

	const User = useSelector((state) => state.auth.user);
	const online = useSelector((state) => state.online.online);
	const ActiveCred = useSelector((state) => state.cred.activeCred);

	useEffect(() => {
		socket.on("message", (message) => {
			if (message.messageType === "notification") {
				if ("serviceWorker" in navigator && "PushManager" in window) {
					navigator.serviceWorker.ready
						.then((registration) => {
							registration.showNotification(message.dataValues.title, {
								body: message.dataValues.note,
								icon: "",
							});
						})
						.catch((err) => console.log(err));
				}
				FetchNotifications(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "staff") {
				FetchStaffs(
					ActiveCred.instLinker,
					User.token,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "mode") {
				FetchModes(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}

			if (message.messageType === "expenseCat") {
				FetchExpenseCats(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "expense") {
				FetchExpenses(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "account") {
				FetchAccounts(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "application") {
				FetchApplications(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "branch") {
				FetchBranchs(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}

			if (message.messageType === "entry") {
				FetchEntrys(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "online-payment") {
				FetchMpesaPayments(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "group") {
				FetchGroups(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}

			if (message.messageType === "client") {
				FetchClients(
					ActiveCred.instLinker,
					User.token,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "payrollCat") {
				FetchPayrollCats(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "payrollItem") {
				FetchPayrollItems(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "payrollEntry") {
				FetchPayrollEntries(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "sub") {
				FetchSubs(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "sms") {
				FetchMessages(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
		});
	}, []);

	return <></>;
};

export default SocketMessages;
